body {
    background-color: black;
    font-family: "Dosis";
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
}

h1 {
   
    margin-top: 20px;
    color: white;
    font-size: 44px;
    letter-spacing: 3px;
}

@media (max-width: 770px) {
    .container {
        margin: 0 10%;
    }

    .image {
        width: 100%;
    }
}