* {
    box-sizing: border-box;
}

.container {
    color: white;
    display: flex;
    width: 100%;
    justify-content: center;
}

.box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: solid white 1px;
    min-widtH: 250px;
    padding: 20px;
    margin: 15px;
    margin-bottom: 0;
}


.category {
    font-size: 24px;
    margin-bottom: 8px;
}

.number {
    font-size: 26px;
    letter-spacing: 2px
}

.infected {
    border: blue solid 1px;
}

.recovered {
    border: green solid 1px;
}

.deaths {
    border: red solid 1px;
}

p {
    text-align: center;
    color: white;
    margin-bottom: 25px;
}

@media (max-width: 770px) {
    .container {
        flex-direction: column;
    }

    .box {
        min-width: 80%;
    }
}